import {devices} from '@/api/device';
import {mapState} from 'vuex';
export default {
	data:function(){
		return {
			devices:[]
		}
	},
	computed:{
		...mapState(["currentUser"])
	},
	onLoad(){
		devices(this.currentUser.OpenId).then(res=>{
			if(res.data && res.data.length > 0){
				this.devices.push(...res.data);
			} else {
				this.devices = [];
			}
		})
	},
}
